<template>
     <section>
        <loader :is-visible="isLoading"></loader>
                                     <div class="border p-3" >                              
                                <dynamic-form v-if="DataLoaded" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                                    <template v-slot:tgslot-GracePeriod="{ data }">
                                       
                                    <div class="input-group">
                                            <input type="text" class="form-control" v-on:keyup="handleChange(GracePeriod)" v-model="GracePeriod" >
                                            <span class="input-group-append">
                                                  <span class="input-group-text" id="spnOutOF">Days</span>
                                              </span>
                                            </div>
                                </template>
                                
                                <template  v-slot:tgslot-tblContainer="{ data }">
                                        <div class="table-responsive" v-if="IsShowCompartments">
                                            <Form ref="AddProductType">
                                                <table id="tblContainerList" 
                                                    class="table table-bordered dt-responsive nowrap mt-0">
                                                    <thead id="divgrid" class="thead-bg">
                                                        <th><span class="float-left">Compartments</span><span
                                                                class="text-danger float-left">*</span></th>
                                                        <th style="min-width:100px;" class="text-center"><span>Action</span>
                                                        </th>
                                                    </thead>
                                                    <tbody>
                                                        <tr id="datarow" v-for="(item, index) in tableRows">
                                                            <td>
                                                                <Field name="CompartmentName" v-slot="{errors}" :rules="{ 'required': true }" >
                                                                    <input :id="'container_name_'+index" type="text" 
                                                                        maxlength="200" :class="{'form-control':true,'is-invalid':errors[0]}" v-on:change="onChangeContainer(data.fieldInfo, index, $event)"
                                                                    v-model="item.InputContainervalue" placeholder="Fill the compartment name">
                                                                    Â <span v-if="errors[0]" name="CompartmentName" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                                </Field>
                                                            </td>
                                                            <td><a  class="round-icon-small btn-danger-light float-none d-inline-block" 
                                                                        href="javascript:;" title="Delete"><em v-on:click="RemoveContainerRow(index)"
                                                                            class="recyclebin fa fa-trash"></em></a>
                                                                    <a class="add round-icon-small btn-success-light float-none d-inline-block" v-on:click="AddContainerRow()"
                                                                        href="javascript:;" title="Add"><em
                                                                     class="fa fa-plus"></em></a>
                                                                </td>
                                                        </tr>
                                                        

                                                    </tbody>
                                                </table>
                                            </Form>
                                            </div>
                                            
                                    </template>
                               
                                </dynamic-form>
                           
                             </div>
        <TagNamePopup v-if="IsTagNamePopup" :FromAddProduct="FromAddProduct"></TagNamePopup>
    </section> 
</template>
<script>
import DataService from '../../service/DataService'
import TagNamePopup from '../Modal/TagNamePopup.vue'


export default {
    props:{
        popupCondition: {
            type: Boolean,
            required: true,
        },
        AseetTypeIde: {
            type: Number,
            required: false
        }
    },
    components: {
        TagNamePopup
    },
    data() {
        return {
            isLoading:false,
            FormSchema: [
                {
                    layoutType: "triple",
                    group_name:'Product Type',
                    group_display_name: this.$t('ItemType'),
                    Data: [
                    
                        {
                            astype: "TextField",
                            label: this.$t('ItemNameType'),
                            name: "ItemNameType",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true},
                            config: {
                                options: [],
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ProductTypeCode'),
                            name: "ProductTypeCode",
                            value: "",
                            placeholder: "",
                            validationRules: {  max:4 }
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('Container'),
                            name: "Container",
                            value:"",
                            placeholder: "",
                            config: {
                                onChange:this.ShowCompartments
                            }
                        },
                        {
                            astype: "SlotField",
                            showLabel:false,
                            label: this.$t('Container'),
                            name: "tblContainer",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            visibility:true,
                        },
                    ],
                },
                    {
                    layoutType: "triple",
                    Data: [
                    {
                            astype: "FileField",                        
                            showLabel:true,
                            label: this.$t('AttachItemImage'),
                            name: "AttachItemImage",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize:5,
                                accept:[
                                    {
                                       fileType:'png',
                                       iconClass:'fa-file-image-o'
                                    },
                                    {
                                       fileType:'jpg',
                                       iconClass:'fa-file-image-o'
                                    },
                                    {
                                       fileType:'jpeg',
                                       iconClass:'fa-file-image-o'
                                    },
                                    {
                                       fileType:'pdf',
                                       iconClass:'fa-file-archive-o'
                                    }
                                ],
                                onChange: this.setFile1,
                                showAddIcon: false,
                                onAddButtonClick: this.AddFileMore,
                                multiple:false
                            },
                        },
                    ],
                },
                {
                    layoutType: "Single",
                    Data:[
                    {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                        },
                    ]
                },
                {
                    layoutType: "four",
                    group_name:'Product Categories',
                    group_display_name: this.$t('ProductCategories'),
                    Data:[
                        {
                            astype: "toggleField",
                            label: this.$t('Asset_No'),
                            name: "Asset_No",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('Consumable'),
                            name: "Consumable",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('License'),
                            name: "License",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('MaterialSpareParts'),
                            name: "MaterialSpareParts",
                            value:"",
                            placeholder: "",
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data:[
                    {
                            astype: "MultiSelectField",
                            label: this.$t('Tags'),
                            name: "Tags",
                            value: "",
                            mode:"tag",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon:true,
                                onAddButtonClick:this.OpenAddTagPopup
                            },
                        },
                    ]
                },
        
                    {
                    layoutType: "four",
                    group_name:'Hardware Integration Type',
                    group_display_name: this.$t('HardwareIntegrationType'),
                    Data:[
                        {
                            astype: "toggleField",
                            label: this.$t('Time_n_Attendance'),
                            name: "Time_n_Attendance",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('Camera'),
                            name: "Camera",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('RFID'),
                            name: "RFID",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('GPSTracking'),
                            name: "GPSTracking",
                            value:"",
                            placeholder: "",
                        },
                    ]},
                    {
                    layoutType: "four",
                    group_name:'Action Based Properties',
                    group_display_name: this.$t('ActionBasedProperties'),
                    Data:[
                        
                        {
                            astype: "toggleField",
                            label: this.$t('IsTransferable'),
                            name: "IsTransferable",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('IsSelfCheckOut'),
                            name: "IsSelfCheckOut",
                            value:"",
                            placeholder: "",
                            config: {
                                onChange:this.OnIsSelfCheckOut
                            },
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('SelfieEnable'),
                            name: "SelfieEnable",
                            value:"",
                            placeholder: "",
                            visibility:false,
                            
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('EmployeeAcceptance'),
                            name: "EmployeeAcceptance",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('EnableTerms'),
                            name: "EnableTerms",
                            value:"",
                            placeholder: "",
                        },
                    
                        {
                            astype: "toggleField",
                            label: this.$t('GPS_Enable'),
                            name: "GPS_Enable",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('Fleet_Management'),
                            name: "Fleet_Management",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('PenaltyOnStatus'),
                            name: "PenaltyOnStatus",
                            value:"",
                            placeholder: "",
                            config: {
                                onChange:this.OnPenaltyOnStatus
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "AssetReplacementType",
                            value: "",
                            placeholder: "",
                            visibility:false,
                            config: {
                                options: [],
                            },
                            validationRules: { "required": true},
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('PenaltyOnReturnOverdue'),
                            name: "PenaltyOnReturnOverdue",
                            value:"",
                            placeholder: "",
                            config: {
                                onChange:this.OnPenaltyOnReturnOverdue
                            },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('GracePeriod'),
                            name: "GracePeriod",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            visibility:false,
                            validationRules: { "required": true}
                        },

                        // {
                        //     astype: "NumericField",
                        //     label: this.$t('GracePeriod'),
                        //     name: "GracePeriod",
                        //     value: "",
                        //     placeholder: "",
                        //     visibility:false,
                        //     validationRules: { "required": true, max: 250 },
                        // },
                    ]},
                    {
                    layoutType: "four",
                    group_name:'Deployment Properties',
                    group_display_name: this.$t('DeploymentProperties'),
                    Data:[
                        {
                            astype: "toggleField",
                            label: this.$t('InsurancePolicy'),
                            name: "InsurancePolicy",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('Calibration'),
                            name: "Calibration",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('ItemCost'),
                            name: "ItemCost",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('IsWarranty'),
                            name: "IsWarranty",
                            value:"",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('Depreciable'),
                            name: "Depreciable",
                            value:"",
                            placeholder: "",
                        },
                    
                    ]
                }
            ],
            buttons: [
                {
                    type: "button",
                    class: "btn btn-secondary",
                    text: "Skip",
                    onClick: this.Skip,
                    isVisible:false,
                    
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    value:"Save",
                 
                   
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save & New",
                    isVisible:false,
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save & Next",
                    isVisible:false,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick:this.CancelButton,
                    isVisible:true,
                    
                },
                
            ],
            StatusDDL: [
                {
                    label: 'Active',
                    value: 1001
                },
                {
                    label: 'Inactive',
                    value: 1002
                },
            ],
            AssetReplacementTypeDDL:[
                {
                    label: 'Damaged',
                    value: 12396
                }, 
                {
                    label: 'In Stocck',
                    value: 12396
                },
                {
                    label: 'Lost',
                    value: 2171
                },
                {
                    label: 'Stolen',
                    value: 12398
                },
            ],
            IsTagNamePopup: false,
            FromAddProduct: false,
             IsShowCompartments: false,
            lbl:false,
            tableRows:[{InputContainervalue:''}],
            currentInputIndex:0,
            //inputs: [],
            //AssetType:'',
            AssetTypeCode:'',
            userId:null,
            companyId:null,
            DataLoaded:0,
            TagsArray:[],
            file:[],
            file1:[],
            ShowAddProduct: false,
            CompartmentListData:[],
            Count:0,
            checkFieldsvalue:false,
            TypeData:[]
        }
    },
    created:  async function()
    {
        debugger;
        var vm=this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.Name;
        await vm.GetTagsByModuleI();
        await vm.BindStatus();
        await vm.AdvancedSetupFields();
        if(vm.AseetTypeIde>0){
            vm.FetchData();
        }
        vm.DataLoaded=1;
    },
    methods: {
        onChangeContainer(fieldInfo,index,e)
        {
            this.FormSchema[0].Data[4].value=e.target._value;
        },
        handleChange(input)
        {
            debugger
            if (input.value < 1) input.value = 1;
            if (input.value > 365) input.value = 365;
        },
        OnPenaltyOnStatus(ev)
        {
            var vm=this;
            if(ev==true){
                vm.FormSchema[6].Data[8].visibility=true;
                vm.AssetReplacementTypeDDL.forEach((itm)=>{
                vm.FormSchema[6].Data[8].config.options.push({
                name:itm.label,
                value:itm.value
            })
        })
            }
            else if(ev==false){
                vm.FormSchema[6].Data[8].visibility=false;
            }
        },
        OnIsSelfCheckOut(ev)
        {
            var vm=this;
            if(ev==true){
                vm.FormSchema[6].Data[2].visibility=true;
            }
            else if(ev==false){
                vm.FormSchema[6].Data[2].visibility=false;
            }
        },
        OnPenaltyOnReturnOverdue(ev)
        {
            var vm=this;
            if(ev==true){
                vm.FormSchema[6].Data[10].visibility=true;
            }
            else if(ev==false){
                vm.FormSchema[6].Data[10].visibility=false;
            }
        },
        Skip:function(){
           var vm=this;
           vm.$parent.SkipTab(3);
        },
        AdvancedSetupFields()
        {
            var vm=this;
            if(vm.popupCondition=='setup')
            {
                vm.buttons[0].isVisible=true;
                vm.buttons[1].isVisible=false;
                vm.buttons[2].isVisible=true;
                vm.buttons[3].isVisible=true;
                vm.buttons[4].isVisible=false;
            }
        },
        async GetTagsByModuleI () {
            
        var vm = this;
        await DataService.GetTagsByModuleId('moduleName=Asset').then(function (response) {
            vm.TagsArray = JSON.parse(response.data).data;
            vm.TagsArray.forEach((itm, index) => {
                vm.FormSchema[4].Data[0].config.options.push({
                   name: itm.TAG_NAME,
                   value: itm.TAG_ID
               })
            })
        })
        },
        OpenAddTagPopup() {
            var vm = this;
            vm.IsTagNamePopup = true;
        },
        setFile1: function (e) {
            
            var vm=this;
            for (let index = 0; index < e.target.files.length; index++) {
                const element = e.target.files[index];
                vm.file1.push(element)
            }
        },
        BindStatus()
        {
            
            var vm=this;
            vm.StatusDDL.forEach((itm)=>
        {
            vm.FormSchema[0].Data[1].config.options.push({
                name:itm.label,
                value:itm.value
            })
        });
        },
        onSubmit: function(formfields,val)
        {
            debugger
            var vm=this;
            vm.isLoading=true;
            vm.Count=0;
            if(formfields.Asset_No == false && formfields.Consumable == false && formfields.License==false  && formfields.MaterialSpareParts==false)
            {
                vm.ShowAlert(vm.$t("At least one Product category must be selected!"), "failure",vm.$t('Alert'));
                vm.Count=1;
            }
            else if(vm.IsShowCompartments==true)
            {
                  
                vm.tableRows.forEach((item,index)=>{
                    if(item.InputContainervalue=='')
                    {
                       vm.checkFieldsvalue==true;
                        vm.Count=1;
                    }
                })
                
            }
                if (vm.Count==0)
                {
                  
                let objCompartmentList={}
                vm.tableRows.forEach((item,index)=>{
                    objCompartmentList={
                    container_Id: 0,
                    asset_type_id: 0,
                    container_name: item.InputContainervalue,
                    status_id: 1001,
                    modified_by: 0,
                    }
                    vm.CompartmentListData.push(objCompartmentList);
                });
                let objAssets={};
                   objAssets.AssetTypeId=0;
                   objAssets.AssetType = formfields.ItemNameType;
                   objAssets.status_id = formfields.Status;
                   objAssets.Description = formfields.Description;
                   objAssets.created_at = null;
                   objAssets.modified_at = null;
                   objAssets.modified_by = vm.userId;
                   objAssets.company_id = vm.companyId,
                   objAssets.AssetTypeCategory = formfields.Asset_No == true ? 1 :0;
               
                   objAssets.IsContainer = formfields.Container == true ? 1 : 0;

                   objAssets.IsSelfCheckOut = formfields.IsSelfCheckOut == true ? 1 : 0;
                   objAssets.IsTransferable = formfields.IsTransferable == true ? 1 : 0;
                   objAssets.IsEnableTerms = formfields.EnableTerms == true ? 1 : 0;
                   objAssets.InsurancePolicy = formfields.InsurancePolicy == true ? 1 : 0;
                   objAssets.AssetTypeCode = vm.AssetTypeCode;
                   objAssets.IsAsset = formfields.Asset_No == true ? 1 :0;
                   objAssets.IsConsumable = formfields.Consumable == true ? 1 : 0;
                   objAssets.IsLicense = formfields.License == true ? 1 : 0;
                   objAssets.IsSpare = formfields.MaterialSpareParts == true ? 1 : 0;
                   objAssets.IsItemCost = formfields.ItemCost == true ? 1 : 0;
                   objAssets.IsDepreciable = formfields.Depreciable == true ? 1 : 0;
                   objAssets.IsCalibration = formfields.Calibration == true ? 1 : 0;
                   objAssets.IsWarranty =  formfields.IsWarranty == true ? 1 : 0;
                   objAssets.IsEmployeeAcceptance = formfields.EmployeeAcceptance == true ? 1 : 0;
                   objAssets.IsPenalty = formfields.PenaltyOnStatus == true ? 1 : 0;
                   var penalty = formfields.PenaltyOnStatus == true ? 1 : 0;
                   objAssets.penalty_type =    penalty == 0 ? null : formfields.AssetReplacementType;
                   objAssets.IsPenaltyOnReturnOverdue = formfields.PenaltyOnReturnOverdue == true ? 1 : 0;
                   var penalty_on_overdue = formfields.PenaltyOnReturnOverdue == true ? 1 : 0;
                   objAssets.GracePeriod = penalty_on_overdue == 0 ? null : formfields.GracePeriod;
                   objAssets.IsGPSTracking = formfields.GPSTracking == true ? 1 : 0;
                   objAssets.IsFleetManagement = formfields.Fleet_Management == true ? 1 : 0;
                   objAssets.IsTimeAttendance = formfields.Time_n_Attendance == true ? 1 : 0;
                   objAssets.IsCamera = formfields.Camera == true ? 1 : 0;
                   objAssets.IsRFID = formfields.RFID == true ? 1 : 0;
                   objAssets.IsGPS = formfields.GPS_Enable == true ? 1 : 0;
                   objAssets.IsSelfieEnable = 1;
                   objAssets.IsSampleData=1;
                   objAssets.container_name='';
                   objAssets.BatchId='';
                   objAssets.AssetTagIds=  Array.prototype.map.call(formfields.Tags, function(item) { return item.value; }).join(",");
                   objAssets.CompartmentList=vm.CompartmentListData;
                   var formData = new FormData();
                var postString = { "postString": objAssets }

                formData.append("postString", JSON.stringify(objAssets));
                formData.append("Files", vm.file1);
                DataService.AddProductType(formData).then(function (response) {
                    
                    if(response.data.msg =="size_exceed" || response.data.msg =="FileSizeExceeded" || response.data.msg =="ExceedAllowedSize")
                        {
                            vm.ShowAlert((vm.$t('size_exceed', [vm.$t('Asset')]), "warning", true, vm.$t("Alert")))
                        }
                    else if(response.data.status=='success')
                    {
                        vm.ShowAlert(vm.$t("Product Type Added Successfully"), "success",vm.$t('Alert'));
                        vm.CancelButton();
                    }
                    else if(response.data.status=="AlreadyExists")
                    {
                        vm.ShowAlert(vm.$t("Already exist record!"), "warning",vm.$t('Alert'));
                        vm.CancelButton();
                    }
                    else{
                        vm.ShowAlert(vm.$t("UnknownError"), "failure",vm.$t('Alert'));
                        vm.CancelButton();
                    }
                })
            }
            vm.isLoading=false;
            
        },
        
        AddContainerRow() {
            var vm=this;
            vm.$refs.AddProductType.validate().then(result=>{
                if(result){
                    this.tableRows.push({InputContainervalue:''})
                }
            })
           
        },
        RemoveContainerRow(index)
        {
            var vm=this;
            if(vm.tableRows.length==1)
            {
                vm.ShowCompartments()
                vm.FormSchema[0].Data[3].value=false;
                vm.lbl=false;
            }
            else
            {
                
                vm.tableRows.splice(index, 1);
            }
        },
        ShowCompartments() {
            debugger
            var vm = this;
            if(vm.lbl==false)
            {
                vm.IsShowCompartments = true;
                vm.lbl=true;
            }
            else if(vm.lbl == true)
            {
                vm.IsShowCompartments = false;
                vm.tableRows=[{InputContainervalue:''}];
                vm.lbl=false;
            }
        },
        CancelButton() {
            debugger
            var vm = this;
            vm.$parent.ShowAddProductType=false;
            vm.$parent.GetAssetType()
        },
        divDialogAddTags() {
            var vm = this;
            vm.FromAddProduct = false;
            vm.IsTagNamePopup = true;
        },
        CloseTagPopup() {
            var vm = this;
            vm.IsTagNamePopup = false;
        },

        FetchData() {
            debugger;
            var vm = this;
            vm.isLoading = true;
            var params = `Id=${vm.AseetTypeIde}`;
            DataService.AssetGetById(params).then(response => {
                if (response.data != null) {
                    if (response.data.length > 0) {
                        vm.TypeData = response.data;                       
                        vm.isLoading = false;
                    }
                    else {                        
                        vm.TypeData = [];                       
                        vm.isLoading = false;
                    }
                }
                else {
                     vm.isLoading=false;
                }                
            });
        },
    }
}
</script>